@import url(https://fonts.googleapis.com/css?family=Comfortaa);
:root {
  --textColor: #212121;
  --primaryWhiteColor: #ffffff;
  --bgColor: #d8bfd8;
  --darkVioletColor: #391d35;
  --violetColor: #613d5c;
  --shadow-dark: gray 1px 1px 10px, gray -1px -1px 10px, gray -1px 1px 10px,
    gray 1px -1px 10px;
  --transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-image: url(/goit-react-hw-07-phonebook/static/media/seruzor68.057651ab.jpg);
  font-family: 'Comfortaa', cursive;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
p {
  margin: 0;
}

h1,
h2,
h3,
button {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

li {
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1 {
  text-align: center;
  font-weight: 700;
  font-size: 25px;
  color: #613d5c;
  color: var(--violetColor);
}

h2 {
  font-weight: 700;
  font-size: 20px;
  color: #613d5c;
  color: var(--violetColor);
}

.Container_container__x32c1 {
  margin: 30px auto;
  padding: 30px;

  width: 500px;
  background-color: var(--bgColor);
  border-radius: 10px;
  box-shadow: var(--shadow-dark);
}

.Loader_overlay__Bj5GJ {
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.ContactForm_form__22VOk {
  margin-top: 20px;
}

.ContactForm_label__1G7L_ {
  display: block;
  margin-bottom: 20px;

  font-size: 17px;
  font-weight: 500;
  color: gray;
}

.ContactForm_input__4OwIb {
  display: block;
  margin-top: 5px;
  padding: 10px;

  width: 100%;
  text-align: center;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.ContactForm_button__29-jF {
  display: block;
  padding: 10px 15px;
  margin-left: auto;
  margin-right: auto;

  color: var(--primaryWhiteColor);
  background-color: var(--darkVioletColor);
  border-radius: 10px;
  border: solid 3px var(--primaryWhiteColor);
  outline: none;

  font-weight: 700;
  font-size: 15px;
  text-transform: uppercase;

  cursor: pointer;
  transition: var(--transition);
}

.ContactForm_button__29-jF:hover {
  transform: scale(1.06);
}

.ErrorImg_wrapper__18Bsr {
  margin: 0 auto;
  padding: 30px 0;
  text-align: center;
}

.ErrorImg_text__2m9FW {
  margin-top: 20px;
  color: red;
  font-weight: 500;
  font-size: 24px;
}

.ErrorImg_img__2pwU0 {
  text-align: center;
}

.ContactList_list__YHztv {
  margin-top: 30px;
  text-align: left;
}

.ContactList_item__2sdPR {
  display: flex;
  margin-top: 15px;

  font-size: 15px;
  font-weight: 700;
  color: var(--textColor);
}

.ContactList_data__1nldN {
  display: flex;
  justify-content: space-evenly;
  text-align: left;
  width: 100%;
}

.ContactList_button__215sh {
  margin-left: 20px;

  display: inline-block;
  width: 25px;
  height: 25px;

  background-image: url(/goit-react-hw-07-phonebook/static/media/cross.6d51e9e0.svg);
  background-repeat: no-repeat;
  background-size: contain;
  background-color: transparent;

  border: solid transparent 5px;
  outline: none;

  border-radius: 50%;
  cursor: pointer;

  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.ContactList_button__215sh:hover {
  background-color: var(--violetColor);
  border: solid var(--violetColor) 5px;
}

.ContactFilter_label__D4skN {
  display: block;
  margin-top: 10px;

  text-align: center;
  font-size: 17px;
  font-weight: 500;
  color: gray;
}

.ContactFilter_input__2UCeG {
  display: block;
  margin-top: 5px;
  padding: 10px;

  width: 100%;
  text-align: center;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

